










import {Component, Vue} from "vue-property-decorator";
@Component({
    name: 'DashboardTabs',
    props: {
        routes: {
            type: Array,
            required: true,
        },
    }
})
export default class DashboardTabs extends Vue {
    isActive(route: any) {
        return route.name === this.$route.name;
    }
}
